@import "../../../styles/sass/variables";
@import "../../../styles/sass/mixin";
.distanceWork__block {
    background-color: $distance-background;
    .container {
        @include container();
        @include flex (center,center,column);
        padding: 100px 0 80px 0;
        .distanceWork__block__title {
            margin-bottom: 20px;
            @media (max-width: 599px) {
                text-align: center;
            }
        }
    }

}



